.Joke {
    display: flex;
    border-bottom: 2px solid #eeeeee;
    align-items: center;
    justify-content: center;
    font-weight: 400;
    padding: 1rem;
}
 .Joke-buttons {
     display: flex;
     align-items: center;
     justify-self: center;
     margin-right: 1rem;
     width: 15%;
 }
  .Joke-text {
      width: 75%;
      font-size: 1.2rem;
  } 

  .Joke-votes {
      width: 50px;
      height: 50px;
      
      line-height: 50px;
      border-radius: 50%;
      text-align: center;
      font-size: 20px;
      border: 3px solid red;
      font-weight: 300;
      box-shadow:  0 10 38px rgba(0, 0, 0, 0.2), 0 10px 12px rgba(0, 0, 0, 0.1);
  }
  .fa-arrow-up, .fa-arrow-down {
      font-size: 1.5em;
      margin: 10px;
      cursor: pointer;

  }

  .Joke-smiley {
      font-size: 3rem;
      margin-left: auto;
      border-radius: 50%;
      box-shadow:  0 10 38px rgba(0, 0, 0, 0.2), 0 10px 12px rgba(0, 0, 0, 0.1);
  }

  