.JokeList {/*makes entire div display flex*/
    display: flex;
    width: 80%;
    height: 80%;
}
.JokeList-sidebar {
    background: #9575cd;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 30%;
    text-align: center;
    box-shadow: 0 19 38px rgba(0,0,0,0.3), 0 15px 12px rgba(0,0,0) inset 0 0 25px #7e57c2;
    z-index: 2;
}
.JokeList-title {
    font-size: 4rem;
    margin: 2rem;
    color: white;
    font-weight: 300;
    letter-spacing: 0.6rem;;
}
 .JokeList-title span {
     font-weight: 700;
     letter-spacing: 0;
 }
 .JokeList-sidebar img {
     width: 50%;
     border-radius: 50%;
 }
 .JokeList-jokes {
     height: 90%;
     background: white;
     align-self: center;
     width: 70%;
     overflow: scroll;
 }
 .JokeList-spinner {
     color: white;
     text-align: center;
 }
 .JokeList-getmore{
     font-size: 1.5rem;
     width: 50%;
     border-radius: 3rem;
     padding: 1rem 2rem;
     color: white;
     font-weight: 700;
     margin: 2rem;
     border: none;
     word-spacing: 2rem;
     outline: none;
     cursor: pointer;
     transition: 0.8s cubic-bezier(0.2, 1, 0.2, 1);
     background: linear-gradient(
    135deg,
      rgba(179,229,252, 1) 0%,
      rgba(179,229,252, 1) 50%,
      rgba(240,98,146, 1) 50%,
      rgba(240,98,146, 1) 100%
  );
  box-shadow: 0 19px 38px rgba(0,0,0,0.3), 0 15px 12px rgba(0,0,0,0.1);
 }
 .JokeList-getmore:hover {
     transform: translateY(-2px);
     box-shadow: 0 15px 30px rgba(0,0,0,0.25);
 }